.nav-wrapper {
  height: 60px;
  background-color: lightblue;
}

.nav {
  width: 70%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.nav a:nth-child(2){
  margin-left: 1em;
  margin-right: 1em;
}

.nav a {
  text-decoration: none;
  color: black;
  font-size: 1.2rem;
}


